export default{
	data(){
		return{
			id:null,
			details:{},
			content:""
		}
	},
	onLoad(e){
		this.id=e.id
	},
	onShow(){
		this.getMsgDetails()
	},
	methods:{
		getMsgDetails(){
			this.$http.post("sMessage/getComplaintDetail",{
				id:this.id
			}).then(res=>{
				if(res.code==100){
					this.details=res.info
					if(this.details.newsVoReply){
						this.content=this.details.newsVoReply.content
					}else{
						this.content=""
					}
				}
			})
		},
		reply(){
			let url=""
			let data={}
			if(this.details.state==1||this.details.state==0){
				// 回复
				url="sMessage/addReply"
			}else{
				// 修改回复的内容
				url="sMessage/updateReply"
				data.id=this.details.newsVoReply.id
			}
			data.replyId=this.details.id			
			data.content=this.content
			if(data.content){						
				this.$http.post(url,data).then(res=>{
					if(res.code==100){
						uni.showToast({
							icon:"success",
							title:"回复成功"
						})
						this.getMsgDetails()
					}else{
						uni.showToast({
							icon:"none",
							title:res.msg
						})
					}
				})				
			}else{
				uni.showToast({
					icon:"none",
					title:'请输入回复内容！'
				})
			}
		}
	}
}
